.user{
    position: absolute;
    display: flex;
    justify-content: center;
    flex-direction: column;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 95%;
}

.user h1{
    margin-bottom: 15px;
    margin-left: 8%;
    width: 300px;
    border-bottom: 2px solid rgb(63, 153, 189);
    color: rgb(63, 153, 189);
}

.user input{
    width: 300px;
    padding: 5px 3px;
    outline: none;
    margin-bottom: 15px;
    margin-left: 8%;
}



@media (max-width: 768px) {
    .user h1{
        margin-left: 1%;
    }
    .user input{
        margin-left: 1%;
    }
}