.userDetail{
    margin-top: 2%;
}

.userDetail h1{
    margin-bottom: 15px;
    margin-left: 5%;
    width: 300px;
    border-bottom: 2px solid rgb(63, 153, 189);
    color: rgb(63, 153, 189);
}

.userInfo{
    display: flex;
    margin: 30px 5% 0 5%;
}

.keys p{
    font-weight: bold;
    width: 200px;
    background-color: rgb(63, 153, 189);
    padding: 10px 10px;
    margin: 4px 0px;
}

.values p{
    padding: 10px 50px;
    width: 800px;
    margin: 4px 4px;
    background-color: rgb(211, 211, 211);
}

.back{
    cursor: pointer;
    font-size: 17px;
    font-weight: bold;
    margin-top: 50px;
    margin-left: 5%;
    padding: 8px 20px;
    border: 2px solid rgb(63, 153, 189);
    color: rgb(63, 153, 189);
    border-radius: 7px;
}

.back:hover{
    background-color: skyblue;
}


@media (max-width: 1024px) {
    .values p{
        width: 600px;
    }
}

@media (max-width: 768px) {
    .values p{
        width: 400px;
    }

    .userInfo{
        overflow-x: scroll;
    }
}